#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 8vw;
}
html {
  width: 100%;
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-family: PingFangSC-Regular, 'microsoft yahei';
  color: #314057;
  font-size: 1.867vw;
  width: 100%;
  height: 100%;
  background: #f1f1f1;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
p,
ul,
li,
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0;
}
ol,
ul,
dt {
  font-weight: normal;
  list-style: none;
}
:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #8492a6;
}
::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #8492a6;
}
input:-ms-input-placeholder {
  color: #8492a6;
}
input::-webkit-input-placeholder {
  color: #8492a6;
}
input[type=number] {
  -moz-appearance: textfield;
}
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.btn:focus,
.btn:active:focus,
.btn.active:focus,
.btn.focus,
.btn:active.focus,
.btn.active.focus {
  outline: none;
}
.verticalAlignMiddle {
  vertical-align: middle;
}
.displayBlock {
  display: block;
}
.displayNone {
  display: none;
}
.inlineBlock {
  display: inline-block;
}
.clearfix::after {
  content: ".";
  clear: both;
  display: block;
  overflow: hidden;
  font-size: 0;
  height: 0;
}
.clearfix {
  zoom: 1;
}
.color-success {
  color: #49c6c2;
}
.color-info {
  color: #329ad9;
}
.color-error {
  color: #fa6c9a;
}
.color-warning {
  color: #f8992b;
}
.float_right {
  float: right;
}
.text-ellipsis2 {
  word-break: break-all;
  text-overflow: ellipsis;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /*! autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.text-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
select {
